.login-container{display:flex;justify-content:space-between;align-items:center;background-color:#bdb9b9;padding:20px;box-shadow:0 4px 8px rgba(0,0,0,.1);height:100%}.login-container .login-title{font-size:2rem;color:#333;text-align:center}.login-container button{background-color:#0070f3;color:#fff;padding:20px 70px;margin:0;border:none;border-radius:5px;cursor:pointer;transition:background-color .3s ease;background-color:#0070f3}.login-container button:hover{background-color:#005bb5}.login-container form{display:flex;justify-content:center;align-items:center;gap:40px}h1{font-size:60px;margin-bottom:20px;color:#000}h4{margin-bottom:5px}hr{width:100%}section{height:auto;display:flex;justify-content:flex-start;align-items:center}section .TitleProjectSection{display:flex;flex-direction:column;justify-content:center;align-items:flex-start;width:100%}section .MenuProjectSection{height:100%;width:100%}