.login-container{display:flex;justify-content:space-between;align-items:center;background-color:#bdb9b9;padding:20px;box-shadow:0 4px 8px rgba(0,0,0,.1);height:100%}.login-container .login-title{font-size:2rem;color:#333;text-align:center}.login-container button{background-color:#0070f3;color:#fff;padding:20px 70px;margin:0;border:none;border-radius:5px;cursor:pointer;transition:background-color .3s ease;background-color:#0070f3}.login-container button:hover{background-color:#005bb5}.login-container form{display:flex;justify-content:center;align-items:center;gap:40px}header{display:flex;justify-content:space-between;align-items:center;background-color:#bdb9b9;height:100px}header .menuBar{display:flex;justify-content:center;align-items:center;height:100%;gap:0;width:auto}header .menuBar div{display:inline-flex;align-items:center;justify-content:center;padding:10px;cursor:pointer;transition:background-color .3s ease;height:100%;width:100px}header .menuBar div svg{transition:transform .3s ease}header .menuBar div:hover svg{transform:scale(1.5)}header .menuBar div.active{background-color:gray}header .menuBar div.active svg{transform:scale(1.5)}